.popup {
    display: none;

    background-color: #090c27;
    overflow: hidden;
    border-radius: rem(20);

    @include below($tablet) {
        padding: rem(50) rem(20)!important;
        background-color: #101542;
    }

    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__input-wrap {
        position: relative;
        width: rem(388);
        margin-bottom: rem(30);
        opacity: 0.4;
        will-change: transform;
        transition: opacity .3s;

        @include below($tablet) {
            width: 100%;
        }

        &::before {
            position: absolute;
            content: '';

            background-size: contain;
            background-repeat: no-repeat;
        }

        &:nth-of-type(1) {
            &::before {
                width: rem(12);
                height: rem(14);
                top: 50%;
                left: rem(6);
                transform: translateY(-50%);
                background-image: url('%=static=%general/persona.png');
            }
        }

        &:nth-of-type(2) {
            &::before {
                width: rem(15);
                height: rem(15);
                top: 50%;
                left: 0;
                transform: translateY(-50%);
                background-image: url('%=static=%general/phone.png');
            }
        }

        &:hover {
            opacity: 1;
        }
    }

    &__input {
        border: none;
        background-color: transparent;
        width: 100%;
        height: 100%;
        padding: rem(20);
        padding-left: rem(26);
        color: #ffffff;
        font-family: "Open Sans";
        font-size: rem(17);
        font-weight: 300;

        border-bottom: rem(1) solid #858287;
        outline: none;



        &::-webkit-input-placeholder {
            color: #ffffff;
            font-family: "Open Sans";
            font-size: rem(17);
            font-weight: 300;
        }

        &::-moz-placeholder {
            color: #ffffff;
            font-family: "Open Sans";
            font-size: rem(17);
            font-weight: 300;
        }

        &:-ms-input-placeholder {
            color: #ffffff;
            font-family: "Open Sans";
            font-size: rem(17);
            font-weight: 300;
        }

        &::placeholder {
            color: #ffffff;
            font-family: "Open Sans";
            font-size: rem(17);
            font-weight: 300;
        }
    }

    &__submit {
        @extend .button;
        @extend .button_simple;

        width: rem(436);
        outline: none;

        @include below($tablet) {
            width: 100%;
            box-shadow: 0 9px 60px rgba(0, 0, 0, 0.75), 0 4px 46px rgba(255, 168, 0, 0.6), 0 5px 14px rgba(255, 186, 0, 0.35), 0 4px 0 #d28500;
            padding: rem(15) rem(24);
            padding-left: calc(50% - 95px);
            padding-right: calc(50% - 95px);
            text-align: center;

            &::after {
                position: absolute;
                content: '';
                top: rem(-69);
                left: calc(100% - 167px);
                width: rem(289);
                height: rem(155);
                background-image: url('%=static=%general/Light_mini.png');
                animation: none;
                animation: lightFull 10s linear infinite;

            }
        }

        span {
            position: absolute;
            top: 50%;
            left: calc(50% - 115px);
            transform: translateY(-50%);
        }
    }

    &__subtitle {
        display: none;
        margin: 0;
        margin-bottom: rem(80);
        color: #dfdfdf;
        font-family: "Open Sans";
        font-size: rem(21);
        font-weight: 400;
        text-align: center;

        @include below($tablet) {
            text-align: left;
        }
    }

    &_see,
    &_want {
        .formrequest__title {
            margin-bottom: rem(29);
        }
        .formrequest__subtitle {
            display: block;
        }
    }

    .checkbox {
        width: rem(388);
        margin-bottom: rem(30);

        @include below($tablet) {
            width: 100%;
        }
    }

    .fancybox-button {
        @include below($tablet) {
            right: rem(5);
            top: rem(10);
        }

        svg {
            color: $white;
        }
    }
}
