.checkbox input {
  position: absolute;
  z-index: -1;
  opacity: 0;
}

.checkbox__text {
  position: relative;
  padding: 0 0 0 25px;
  margin: 0;
  color: #ededed;
  font-family: "Open Sans";
  font-size: 1.0625rem;
  font-weight: 300;
  letter-spacing: 0.17px;
  cursor: pointer;
}

.checkbox input:checked + .checkbox__text:after {
  opacity: 1;
}

.checkbox__text:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0.1875rem;
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 2px;
  border: 0.0625rem solid #4e4d69;
}

.checkbox__text:after {
  content: '';
  left: -1.375rem;
  top: -0.3125rem;
  width: 4.5625rem;
  height: 3.125rem;
  position: absolute;
  background-image: url("../img/general/checkbox.png");
  background-size: contain;
  opacity: 0;
  transition: opacity .3s;
}

.button, .firstscreen__button, .formrequest__submit, .inner-navigation__link, .navigation__item-link, .popup__submit {
  position: relative;
  height: 63px;
  padding: 1.5rem;
  display: block;
  padding-top: 1.375rem;
  text-shadow: 0 1px 0 #a06200;
  color: #ffffff;
  font-family: "Open Sans";
  font-size: 1.1875rem;
  font-weight: 700;
  line-height: 1;
  background: linear-gradient(226deg, #febe20 0%, #f99c06 100%);
  border-radius: 2.5rem;
  box-shadow: 0 9px 60px rgba(0, 0, 0, 0.75), 0 4px 46px rgba(255, 168, 0, 0.6), 0 5px 14px rgba(255, 186, 0, 0.35), 0 4px 0 #d28500;
  text-decoration: none;
  text-align: center;
  border: none;
  will-change: transform;
  z-index: 4;
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

.button::after, .firstscreen__button::after, .formrequest__submit::after, .inner-navigation__link::after, .navigation__item-link::after, .popup__submit::after {
  position: absolute;
  z-index: -2;
  content: '';
  top: -4.3125rem;
  left: 7.375rem;
  width: 18.0625rem;
  height: 9.6875rem;
  background-image: url("../img/general/Light_mini.png");
  background-size: contain;
  animation: light 10s linear infinite;
  -webkit-tap-highlight-color: transparent;
}

.button_simple, .formrequest__submit, .popup__submit {
  box-shadow: none;
}

.button_simple::after, .formrequest__submit::after, .popup__submit::after {
  position: absolute;
  content: '';
  top: -4.3125rem;
  left: 16.5rem;
  width: 18.0625rem;
  height: 9.6875rem;
  background-image: url("../img/general/Light_mini.png");
  animation: lightBig 10s linear infinite;
}

@keyframes light {
  0% {
    transform: translateX(0);
  }
  40% {
    transform: translateX(-253px);
  }
  45% {
    transform: translate(-271px, 30px);
  }
  50% {
    transform: translate(-253px, 60px);
  }
  90% {
    transform: translate(0, 60px);
  }
  95% {
    transform: translate(23px, 30px);
  }
  100% {
    transform: translate(0px, 0);
  }
}

@keyframes lightMicro {
  0% {
    transform: translateX(0);
  }
  40% {
    transform: translateX(-130px);
  }
  45% {
    transform: translate(-148px, 17.5px);
  }
  50% {
    transform: translate(-130px, 35px);
  }
  90% {
    transform: translate(0, 35px);
  }
  95% {
    transform: translate(18px, 17.5px);
  }
  100% {
    transform: translate(0px, 0);
  }
}

@keyframes lightBig {
  0% {
    transform: translateX(0);
  }
  40% {
    transform: translateX(-399px);
  }
  45% {
    transform: translate(-417px, 30px);
  }
  50% {
    transform: translate(-399px, 60px);
  }
  90% {
    transform: translate(0, 60px);
  }
  95% {
    transform: translate(22px, 30px);
  }
  100% {
    transform: translate(0px, 0);
  }
}

@keyframes lightSmall {
  0% {
    transform: translateX(0);
  }
  40% {
    transform: translateX(-206px);
  }
  45% {
    transform: translate(-225px, 30px);
  }
  50% {
    transform: translate(-206px, 60px);
  }
  90% {
    transform: translate(0, 60px);
  }
  95% {
    transform: translate(20px, 30px);
  }
  100% {
    transform: translate(0px, 0);
  }
}

@keyframes lightFull {
  0% {
    transform: translateX(0);
  }
  40% {
    transform: translateX(calc(-100vw + 54px));
  }
  45% {
    transform: translate(calc(-100vw + 32px), 30px);
  }
  50% {
    transform: translate(calc(-100vw + 54px), 60px);
  }
  90% {
    transform: translate(0, 60px);
  }
  95% {
    transform: translate(15px, 30px);
  }
  100% {
    transform: translate(0px, 0);
  }
}

